<template>
  <div class="container">
    <div class="toptip">
      <span>欢迎登录</span>
    </div>
    <div class="centerbox">
      <van-cell-group>
        <van-field
          v-model="loginModel.tel"
          :error="usertel"
          type="number"
          maxlength="11"
          center
          clearable
          placeholder="请输入手机号"
          label="手机号"
        />
        <van-field
          v-model="loginModel.password"
          type="password"
          maxlength="6"
          center
          clearable
          placeholder="请输入密码"
          label="密码"
        />
      </van-cell-group>
      <div style="margin: 16px;">
        <van-button
          round
          block
          type="info"
          :loading="loading"
          loading-text="登录中..."
          @click="login"
        >登录</van-button>
      </div>
      <div class="pwdreg">
        <span @click="gopwd">忘记密码</span>
        <span>|</span>
        <span @click="goreg">前往注册</span>
      </div>
    </div>
    <div class="foottip">
      <span>登录代表您已同意</span>
      <span class="xieyi" @click="goxieyi">用户协议和隐私协议</span>
    </div>
  </div>
</template>

<script>
import { validateTel } from "@/utils/validator";
import { Toast } from "vant";
import { userLogin } from '@/api/user'
import { mapMutations } from 'vuex'
export default {
  name: "login",
  data() {
    return {
      loading: false,
      loginModel: {
        tel: "",
        password: ""
      },
      usertel: false
    };
  },
  watch: {
    tel(value) {
      if (value.length === 11) {
        if (validateTel(value)) {
          this.usertel = false;
        } else {
          this.usertel = true;
          Toast("手机号格式错误");
        }
      } else {
        this.usertel = false;
      }
    }
  },
  methods: {
    ...mapMutations(['loginin']),
    async login() {
      if (!this.loginModel.tel) {
        Toast("手机号不能为空");
        return;
      }
      if (!validateTel(this.loginModel.tel)) {
        Toast("手机号格式错误");
        return;
      }
      if (!this.loginModel.password) {
        Toast("密码不能为空");
        return;
      }
      this.loading = true;
      let res = await userLogin(this.loginModel);
      if (res) {
        if (res.type === 1) {//登录成功
          await this.loginin(res.data)
          setTimeout(() => {
            this.loading = false;
            this.$router.push("/chat");
          }, 1000);
          this.$socket.emit('login', res.data)
        } else if (res.type === 2) {//密码错误
          this.loading = false;
        } else if (res.type === 3) {//登录出错
          this.loading = false;
        } else if (res.type === 0) {//出错
          this.loading = false;
        }
        Toast(res.message);
      } else {
        Toast("似乎出现了错误");
        this.loading = false;
      }
      this.loginModel.tel = ""
      this.loginModel.password = ""
    },
    gopwd() {
      this.$router.push("/forgetpwd");
    },
    goreg() {
      this.$router.push('/register')
    },
    goxieyi() {
      this.$router.push('/xieyi')
    },
  },
};
</script>
<style lang="scss" scoped>
.pwdreg {
  position: relative;
  margin-top: 20px;
  text-align: center;
  span {
    margin: 0 5px 0 5px;
  }
}
.toptip {
  position: relative;
  width: 100%;
  height: 100px;
  padding: 10px 0 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  span {
    width: 100%;
    text-align: center;
    font-size: 32px;
  }
}
.contaiter {
  height: 100vh;
}
.foottip {
  position: fixed;
  bottom: 20px;
  left: 0;
  right: 0;
  text-align: center;
  span {
    font-size: 14px;
  }
  .xieyi {
    color: red;
  }
}
</style>